<template>
  <section class="login-container">
    <div class="login-inner">
      <div class="login-header">
        <h2 class="login-logo"></h2>
        <div class="login-header-title">
          <a href="javascript:;" :class="{on: loginWay}" @click="loginWay=true"></a>
          <a href="javascript:;" :class="{on: !loginWay}" @click="loginWay=false"></a>
        </div>
      </div>
      <div class="login-content">
        <form @submit.prevent="login" autocomplete="off">
          <div :class="{on: loginWay}">
            <section class="login-message">
              <input type="tel" maxlength="11" placeholder="" v-model="phone">
              <button
                class="get-verification"
                :disabled="!rightPhone"
                :class="{right_phone: rightPhone}"
                @click.prevent="getCode">
                {{ computeTime > 0 ? `(${computeTime})s` : '' }}
                </button>
            </section>
            <section class="login-verification">
              <input type="tel" maxlength="8" placeholder="" v-model="code">
            </section>
            <section class="login-hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>
          <div :class="{on: !loginWay}">
            <section class="login-message">
              <input type="text" maxlength="11" placeholder="//" v-model="name">
            </section>
            <section class="login-verification">
              <input type="text" maxlength="8" placeholder="" v-if="showPwd" v-model="pwd">
              <input type="password" maxlength="8" placeholder="" v-else v-model="pwd">
              <div class="switch-button" :class="showPwd ? 'on' : 'off'" @click="showPwd = !showPwd">
                <div class="switch-circle" :class="{right: showPwd}"></div>
                <span class="switch-text">{{ showPwd ? 'abc' : '...' }}</span>
              </div>
            </section>
            <section class="login-message">
              <input type="text" maxlength="11" placeholder="" v-model="captcha">
              <img class="get-verification" src="http://localhost:4000/captcha" alt="captcha" @click="getCaptcha" ref="captcha">
            </section>
          </div>
          <button class="login-submit"></button>
        </form>
        <a href="javascript:;" class="about-us"></a>
      </div>
      <a href="javascript:" class="go-back" @click="$router.back()">
        <i class="iconfont icon-left"></i>
      </a>
    </div>
    <AlertTip :alertText="alertText" v-show="alertShow" @closeTip="closeTip" />
  </section>
</template>

<script>
import AlertTip from '../../components/AlertTip/AlertTip'
import {
  reqSendCode,
  reqSmsLogin,
  reqPwdLogin
} from '../../api'

export default {
  data () {
    return {
      loginWay: true, // true: , false: 
      computeTime: 0, // 
      showPwd: false, // 
      phone: '', // 
      code: '', // 
      name: '', // 
      pwd: '', // 
      captcha: '', // 
      alertText: '', // 
      alertShow: false // 
    }
  },

  computed: {
    rightPhone () {
      return /^1\d{10}$/.test(this.phone)
    }
  },

  methods: {
    // 
    async getCode () {
      // 
      if (!this.computeTime) {
        // 
        this.computeTime = 30
        this.timeId = setInterval(() => {
          this.computeTime--
          if (this.computeTime <= 0) {
            clearInterval(this.timeId)
          }
        }, 1000)

        //  ajax ()
        const result = await reqSendCode(this.phone)
        if (result.code === 1) {
          // 
          this.showAlert(result.msg)
          // 
          if (this.computeTime) {
            this.computeTime = 0
            clearInterval(this.timeId)
            this.timeId = undefined
          }
        }
      }
    },
    showAlert (alertText) {
      this.alertShow = true
      this.alertText = alertText
    },
    // 
    async login () {
      let result
      // 
      if (this.loginWay) { // 
        const {rightPhone, phone, code} = this
        if (!rightPhone) {
          // 
          this.showAlert('')
          return
        } else if (!/^\d{6}$/.test(code)) {
          // 6
          this.showAlert('6')
          return
        }
        //  ajax 
        result = await reqSmsLogin({phone, code})
      } else { // 
        const { name, pwd, captcha } = this
        if (!name) {
          // 
          this.showAlert('')
          return
        } else if (!pwd) {
          // 
          this.showAlert('')
          return
        } else if (!captcha) {
          // 
          this.showAlert('')
          return
        }
        //  ajax 
        result = await reqPwdLogin({name, pwd, captcha})
      }

      // 
      if (this.computeTime) {
        this.computeTime = 0
        clearInterval(this.timeId)
        this.timeId = undefined
      }

      // 
      if (result.code === 0) {
        const user = result.data
        //  user  vuex  state
        // 
        this.$store.dispatch('recordUser', user)
        this.$router.replace('/profile')
      } else {
        // 
        this.getCaptcha()
        // 
        const msg = result.msg
        this.showAlert(msg)
      }
    },
    // 
    closeTip () {
      this.alertShow = false
      this.alertText = ''
    },
    // 
    getCaptcha () {
      //  src 
      this.$refs.captcha.src = 'http://localhost:4000/captcha?time=' + Date.now()
    }
  },

  components: {
    AlertTip
  }
}
</script>

<style lang="stylus" rel="stylesheet.stylus">
.login-container
  width 100%
  height 100%
  background #fff
  overflow hidden
  .login-inner
    padding-top 60px
    width 80%
    margin 0 auto
    .login-header
      .login-logo
        font-size 40px
        font-weight bold
        color #02a774
        text-align center
      .login-header-title
        padding-top 40px
        text-align center
        >a
          color #333
          font-size 14px
          padding-bottom 4px
          &:first-child
            margin-right 40px
          &.on
            color #02a774
            font-weight 700
            border-bottom 2px solid #02a774
    .login-content
      >form
        >div
          display none
          &.on
            display block
          input
            width 100%
            height 100%
            padding-left 10px
            box-sizing border-box
            border 1px solid #ddd
            border-radius 4px
            outline 0
            font 400 14px Arial
            &:focus
              border 1px solid #02a774
          .login-message
            position relative
            margin-top 16px
            height 48px
            font-size 14px
            background #fff
            .get-verification
              outline none
              position absolute
              top 50%
              right 10px
              transform translateY(-50%)
              border 0
              color #ccc
              font-size 14px
              background transparent
              &.right_phone
                color #000
          .login-verification
            position relative
            margin-top 16px
            height 48px
            font-size 14px
            background #fff
            .switch-button
              width 30px
              height 16px
              line-height 16px
              color #fff
              font-size 12px
              border 1px solid #ddd
              padding 0 6px
              border-radius 8px
              transition background-color .3s, border-color .3s
              position absolute
              top 50%
              right 10px
              transform translateY(-50%)
              &.off
                background #fff
                .switch-text
                  float right
                  color #ddd
              &.on
                background #02a774
              >.switch-circle
                // transform translateX(27px)
                width 16px
                height 16px
                position absolute
                top -1px
                left -1px
                border 1px solid #ddd
                border-radius 50%
                background #fff
                box-shadow 0 2px 4px 0 rgba(0,0,0,.1)
                transition transform .3s
                &.right
                  transform translateX(27px)
          .login-hint
            margin-top 12px
            color #999
            font-size 14px
            line-height 20px
            >a
              color #02a774
        .login-submit
          display block
          width 100%
          height 42px
          margin-top 30px
          border-radius 4px
          background #4cd96f
          color #fff
          text-align center
          font-size 16px
          line-height 42px
          border 0
      .about-us
        display block
        font-size 14px
        margin-top 20px
        text-align center
        color #999
    .go-back
      position absolute
      top 5px
      left 5px
      width 30px
      height 30px
      >.iconfont
        font-size 20px
        color #999
</style>



